import { render } from './render.js';

export class Element {
  get elementsManager(){
    return this.$.app.elementsManager
  }
  constructor(attrs, $) {
    this.$ = $
    this.elementsManager.onCreateElement(this, $.tag, attrs)
  }
  done() {
    this.elementsManager.onDoneElement(this)
    this.$ = null
  }
  set(delta) {
    this.delta = this.delta ? Object.assign(this.delta, delta) : delta;
    return this.$.nodes || delta && Object.keys(delta).length;
  }
  render() {
    this.elementsManager.onRenderElement(this, this.delta, this.$.content, render)
    this.delta = null;
  }
}